            title: 'Garimpo ilegal degrada área igual a 200 campos de futebol na Terra Yanomami em três meses',
            desc: 'Levantamento feito pela Hutukara Associação Yanomami foi apresentado nesta terça-feira',
            text: 'O garimpo ilegal na Terra Yanomami, localizada na região Norte de Roraima, degradou cerca de 200 hectares de floresta -- o equivalente a 200 campos de futebol -- somente no primeiro trimestre deste ano. A informação foi divulgada pela Hutukara Associação Yanomami (HUY), por meio de relatório apresentado nesta terça-feira (25). <br><br> Imagens aéreas na região mostram áreas devastadas pelo avanço do garimpo ilegal. Segundo o documento, que o G1 teve acesso, foram registradas "crateras profundas, acampamentos colados a aldeias e até restaurante de garimpeiros". <br><br> Ao total, imagens de satélite indicam cerca de 2.430 hectares destruídos pelo garimpo. A associação nomeou o resultado da exploração ilegal na área indígena como a nova "Serra Pelada", lugar que ficou conhecido como o maior garimpo a céu aberto do mundo, localizado no Sudeste do Pará. <br><br> O documento também detalha todos os pontos da região onde há indícios evidentes da atividade mineradora ilegal. A primeira rota foi direcionada à porção central da Terra Yanomami, sobre os rios Apiaú, Catrimani e Ajarani e na Serra da Estrutura. <br><br> Já a segunda rota sobrevoou as comunidades de Aracaçá, Homoxi, Kayanau, Palimiú, Papiú, Parima, Waikás e Xitei. <br><br> A Terra Yanomami é palco de diversos ataques há anos. Em 1993, garimpeiros promoveram uma chacina contra os Yanomami, na qual 16 indígenas morreram. O episódio ficou conhecido como o Massacre de Haximu, tendo sido o primeiro caso de genocídio reconhecido pela Justiça brasileira. <br><br> Em junho do ano passado, outro conflito na Terra resultou na morte de dois jovens indígenas, de 20 e 24 anos. De acordo com o Conselho Distrital de Saúde Indígena Yanomami e Yekuana (Condisi-Y), os jovens estavam em um grupo de cinco indígenas quando se depararam com dois garimpeiros armados próximo a uma pista clandestina para pouso de helicóptero. <br><br> Neste mês de maio, a região mais uma vez sofreu diversos ataques de garimpeiros armados. O caso, que iniciou no dia 10, teve dez dias seguidos de conflitos. Na ocasião, três garimpeiros morreram e quatro ficaram feridos, além de um indígena baleado de raspão na cabeça, na comunidade Palimiú, às margens do rio Uraricoera. <br><br> Segundo a HAY, o conflito teria sido em retaliação, após os Yanomami instalarem uma barreira sanitária, para impedir que os garimpeiros usassem o rio Uraricoera para chegar a um de seus acampamentos. <br><br> Cinco dias após esse ataque, líderes indígenas relataram a morte de duas crianças Yanomami, de 1 e 5 anos. Conforme informação divulgada pela Hutukara, os corpos das vítimas foram encontrados boiando no rio, no dia 12 de maio. <br><br> Em nota divulgada à impressa, a associação acredita que, após os tiros, muitas crianças correram assustadas para se proteger, se perderam no mato e ficaram desaparecidas. A informação foi relatada, inclusive, ao Ministério Público Federal (MPF). <br><br> A estimativa é que mais de 20 mil garimpeiros atuam infiltrados na Terra Yanomami. A área é o maior território indígena do país dividido entre Roraima e o Amazonas. Segundo o Instituto Socioambiental (ISA), há 26.780 indígenas vivendo na região. <br><br> Para a organização, essas invasões de garimpeiro às Terras só serão controladas se houver operações "mais abrangentes e eficientes na destruição da estrutura de apoio à atividade ilegal". <br><br> A Hutukara sugere como métodos eficazes, a explosão de pistas e destruição de currutelas, inutilização do maquinário como motores, balsas, aeronaves, helicópteros e quadriciclos, além da "descapitalização" de investidores do setor. <br><br> “Não se trata de um problema sem solução. O estado possui todas as condições para fazer valer a lei e promover a neutralização dos crimes praticados pelo garimpo contra os indígenas da TI Yanomami e o restante da sociedade brasileira”, reforçou o relatório. <br><br> Ainda conforme o relatório, "a crise econômica e o preço do metal estimulam os garimpeiros à atividade exploradora". A organização afirma que a presença dos garimpeiros na região, promove o desmatamento, a contaminação dos rios por mercúrio, a violência contra homens, mulheres e crianças indígenas e a disseminação da Covid-19 no território indígena. <br><br> Conforme o último boletim da Secretaria de Saúde (Sesau) dessa segunda-feira (24), já são 5.739 indígenas infectados e outros 83 mortos por conta do vírus. Os dados são dos Distritos Sanitários Especiais Indígenas (DSEI) Leste e Yanomami.',
