            title: 'Ameaçado de extinção, lobo-guará é visto circulando no centro de cidade em MT',
            desc: 'Chegada do animal na cidade pode ter sido ocasionada em decorrência de impactos ambientais em áreas de mata.',
            text: 'Um lobo-guará foi flagrado próximo a uma escola no Centro de Sinop, no norte do estado, nesta terça-feira (25). A espécie é considerada vulnerável e está ameaçada de extinção. O lobo-guará estampa a nota de R$ 200. <br><br> Segundo os moradores da região, até a publicação desta matéria, o animal não tinha sido resgatado. Algumas pessoas fizeram publicações nas redes sociais pedindo ajuda para encontrá-lo e levá-lo para um local seguro. <br><br> A professora e pesquisadora do curso de veterinária da Universidade Federal de Mato Grosso (UFMT), Elaine Dione, disse que a chegada do lobo-guará na cidade pode ter sido ocasionada em decorrência de impactos ambientais em áreas de mata, como o desmatamento. <br><br> “Ele ocorre em nossa região, mas não gosta de ambiente urbano. Eles se "arriscam" mais em busca de alimento. Os lobos-guarás são animais solitários e arredios à presença humana. Essa ocorrência é incomum”, explicou. <br><br> O Corpo de Bombeiros e a concessionário Rota do Oeste foram acionados na tarde desta terça-feira devido a uma possível travessia do animal na BR, o que o coloca em um risco de vida ainda maior. <br><br> Apesar do porte e da aparência, o lobo-guará é um animal inofensivo ao homem, de comportamento dócil, e raramente há briga entre eles. <br><br> No passado, acreditava-se que a diminuição da população do lobo-guará no Brasil era decorrente da perda de habitat. Mas em 2005 o resultado de um encontro de pesquisadores revelou que o preconceito, desconhecimento e superstição superam a ameaça da diminuição do território. <br><br> O lobo-guará ainda é caçado para a retirada dos olhos como amuleto e por donos de propriedades rurais, com o argumento de evitar o ataque a aves e pequenos animais.',
