            title: 'Seres humanos podem viver até 150 anos, diz estudo',
            desc: 'Um novo estudo realizado pela Gero, apontou que a idade máxima que um ser humano conseguiria viver naturalmente seria de 150 anos.',
            text: 'Utilizando amostras de sangue de voluntários do Reino Unido e dos Estados Unidos, os pesquisadores foram capazes de criar um programa de Inteligência Artificial utilizando dois fatores principais ligados à longevidade: estilo de vida e resiliência. Os resultados foram publicados na revista Nature Communications. <br><br> Por meio do estudo, os cientistas concluíram que estresse, doenças e exercícios podem afetar o quanto uma pessoa pode viver. <br><br> Já a resiliência, por sua vez, está relacionada à maneira como o corpo responde aos itens mencionados anteriormente, visto que a rapidez com que o organismo consegue se recuperar de lesões é crucial para vivermos muitos anos. <br><br> Com um aplicativo criado para celulares iPhone, os especialistas conseguiram estabelecer um banco de dados para estimar o quanto cada usuário poderá viver. Isso só foi possível graças ao "indicador dinâmico do estado do organismo", que apresenta características importantes de cada pessoa para finalizar o cálculo. <br><br> "Este trabalho é uma demonstração de como conceitos emprestados das ciências físicas podem ser usados na biologia para sondar diferentes aspectos da senescência e da fragilidade para produzir fortes intervenções contra o envelhecimento", disse Peter Fedichev, fundador da Gero, ao Daily Mail. <br><br> É importante destacar que, conforme explicou Fedichev, medicamentos, manipulação genética e até mesmo restrição calórica poderão ser estudadas no futuro para melhorar a estimativa de vida média e prolongar alguns anos. Entretanto, o limite continuaria o mesmo, dado que não existe um "elixir da juventude".',
