            title: 'Campeão da 2ª divisão de Portugal, zagueiro Hugo Gomes entra na mira do Inter',
            desc: 'Colorado busca um zagueiro para a sequência da temporada',
            text: 'Em busca de reforços para a defesa, o Inter fez uma sondagem ao zagueiro Hugo Gomes, ex-São Paulo e campeão da 2ª divisão do Campeonato Português pelo Estoril Praia. A tratativa está em estágio inicial, apenas com contato feito para buscar informações sobre a situação do jogador. <br><br> Por ora, não existe negociação efetiva em andamento. Até porque o atleta só poderá ser inscrito a partir da reabertura da janela de transferências internacionais, em 1º de agosto. <br><br> A primeira janela encerrou no último domingo, 23 de maio. Mesmo assim, há perspectivas de que as conversas avancem nos próximos dias, conforme apurado pelo ge. <br><br> Hugo tem 26 anos e pertence ao Famalicão, de Portugal, e atuou emprestado ao Estoril. O jogador tem contrato com o clube português até 30 de junho, quando fica livre de vínculo. Assim, ele já pode assinar um pré-contrato com outra equipe. O São Paulo tem um percentual do atleta. <br><br> Hugo foi formado nas categorias de base do Tricolor Paulista Ele chegou a ser comparado ao ex-zagueiro e ídolo do clube Lugano. Mas não correspondeu às expectativas e sequer estreou pelo profissional do São Paulo. <br><br> Acabou emprestado a Mallorca e Granada, da Espanha, e depois ao Atlético-GO antes de encerrar seu vínculo com o São Paulo. Do Tricolor, ele rumou ao Famalicão e teve também uma passagem pelo Varzim. O Estoril foi o último clube em que teve mais destaque. <br><br> Reforçar a defesa é uma das prioridades do Inter para o Brasileirão. Atualmente, o clube conta apenas com quatro opções para o setor: Víctor Cuesta, Lucas Ribeiro, Zé Gabriel e Pedro Henrique. Rodrigo Moledo se recupera de lesão e só voltará a atuar em 2022. <br><br> Recentemente, o clube fez sondagens a GIl e Jemerson, do Corinthians. A diretoria busca um defensor com perfil de maior experiência e vitalidade para atuar ao lado de Cuesta.',
            img: require('../assets/news/hugo-gomes.jpg'),
