            title: '"Trem de satélites" da SpaceX cruza céu de Brasília',
            desc: 'Momento foi registrado por astrofotógrafo da capital.',
            text: 'O "trem de satélites" da empresa Space X voltou a ser visto no céu de Brasília na noite desta quarta-feira (assista vídeo acima). Os equipamentos fazem parte de um projeto chamado "Starlink", lançado pelo dono da montadora de carros elétricos Tesla, Elon Musk. <br><br> A passagem durou quatro minutos e foi registrada às 18h45 por um morador da capital, o astrofotógrafo Léo Caldas. Em agosto do ano passado, também houve registros da passagem pela capital federal. Moradores das cidades de Juiz de Fora e Belo Horizonte, em Minas Gerais, e no litoral de São Paulo também relataram o fenômeno. <br><br> Os equipamentos foram lançados por foguetes Falcon9 no último dia 9 de maio e, por estarem juntos e enfileirados no céu, ganharam o nome de "trens de satélite". <br><br> Segundo o professor de física e astronomia da Universidade de Brasília (UnB) Paulo Brito, o projeto da SpaceX visa oferecer internet de banda larga para pontos remotos do planeta. <br><br> "São vários conjuntos de 60 satélites. Eles são independentes, ficam rodopiando no planeta e passam diversas vezes por nós. Quando coincidem de passar por Brasília no início da noite ou fim da madrugada, a gente consegue ver." <br><br> Ainda de acordo com o astrônomo, o projeto é ambicioso e pretende lançar até 42 mil satélites no espaço. "Com isso, é possível que, daqui a alguns anos, tenhamos sinal de internet no meio da Amazônia e em pleno Oceano Atlântico", explicou Brito. <br><br> Par o registro, o astrofotógrafo brasiliense Léo Caldas usou uma lente de 35 milímetros e com capacidade para captar bastante luz. Como a passagem durou quatro minutos, Caldas acelerou o vídeo em quatro vezes, reduzindo a duração da gravação. <br><br> Após postar o vídeo nas redes sociais, internautas contaram reações ao avistarem o fenômeno. Em um dos comentários, um homem contou ter visto as luzes no céu e achado que eram drones. <br><br> "Minha filha me chamou no quintal perguntando se estrela se movia e me mostrou a fila de "estrelas" atravessando o céu. Sensacional! Não sabia o que era, achei que fossem drones", escreveu.',
