            title: 'Polícia Civil prende suspeito de receptar e adulterar Fuscas furtados de colecionadores em São Paulo',
            desc: 'Três carros furtados foram encontrados na garagem da casa onde mora homem de 31 anos.',
            text: 'A Polícia Civil prendeu na quarta-feira (20) um homem suspeito de receptar e adulterar Fuscas de colecionadores que foram furtados em São Paulo. <br><br> Três desses carros furtados foram encontrados na garagem da casa onde mora Eduardo de Souza, de 31 anos, na Zona Sul da capital. Ele foi preso em flagrante pelos crimes de receptação e adulteração de veículos. <br><br> Eduardo também é investigado por suspeita de integrar uma quadrilha especializada em furtar Fuscas na cidade. O G1 não conseguiu localizar sua defesa para comentar o assunto até a última atualização desta reportagem. <br><br> Vídeo gravado a partir de câmera de segurança de um estacionamento na Zona Oeste de São Paulo mostra o momento que um Fusca é furtado em 30 de abril por um homem ainda não identificado <br><br> As imagens mostram o ladrão saindo de um Fiat Punto vermelho e entrando no estacionamento. Depois ele deixa o local levando o Fusca. Depois o comparsa também vai embora com o Punto. <br><br> Foi analisando as filmagens que a equipe do 14º Distrito Policial (DP), em Pinheiros, conseguiu localizar o Fusca furtado na garagem de um imóvel na Zona Sul. Com as imagens, a investigação identificou a placa do Punto. Depois chegou ao nome do proprietário e seu endereço. <br><br> ',
