            title: "Starlink: satélites da SpaceX criam luzes no céu do Brasil",
            desc: "Assim como em outras oportunidades, os satélites foram vistos trafegando enfileirados e iluminando o céu do Brasil.",
            text: "Nesta quarta-feira (19), por volta das 18h30, os satélites Starlink puderam ser vistos 'caminhando' pelo céu do Brasil. Vistos em oportunidades anteriores, os satélites fazem parte do projeto da SpaceX de criar uma rede global de internet. <br> <br> Assim como em outras oportunidades, os satélites foram vistos trafegando enfileirados e iluminando o céu do Brasil. A SpaceX já abriu uma página de inscrição do serviço para usuários brasileiros, embora não tenha detalhado quando ou se a internet, de fato, será disponibilizada no país. Brasileiros que puderam acompanhar a passagem dos satélites têm publicado a experiência em redes sociais. Os satélites da Starlink podem ser acompanhados pela plataforma Find Starlink, disponível tanto na web quanto para aplicativos de Android e iOS. Essa é uma boa ferramenta para acompanhar as passagens e capturar imagens. <br> <br> Como os satélites se movimentam rapidamente, é possível que muitos acabem não o vendo em todas as oportunidades — além do fato de que o tempo pode impactar a visibilidade. Normalmente, eles voam nessa formação alinhada entre 30 e 40 unidades. Nos lançamentos, cada vez mais tradicionais, a SpaceX costuma enviar grupos de 60 satélites para a órbita.",
