            text: "Estes foram os números do concurso 2373 da Mega-Sena sorteados pela Caixa nesta quarta-feira (19): 23-24-26-44-49-60. Quem cravar todas as seis dezenas receberá um prêmio principal de R$ 40.188.006,26. A Caixa divulgará em breve se sortudos cravaram os números por meio da página da Mega-Sena na internet. <h2>Quanto custa apostar na Mega-Sena?</h2> Depende de quantos números você pretende colocar no jogo. A aposta mínima agora custa R$ 4,50, e você tem direito de escolher seis dezenas de 1 a 60. Se quiser colocar um número a mais para aumentar as chances de acerto, o preço do jogo sobe para R$ 31,50. No cenário mais caro, com 15 números no volante, a aposta chega a custar R$ 22.522,50",
